/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<< tablet >>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
@media (max-width: 1199px) {
  .nav__logo,
  .nav__logo-img {
    min-width: 86px;
