    top: 11px;
    left: 13px;
    width: 26px;
    height: 2px;
    background-color: #afafaf;
    box-shadow: 0 8px 0 0 #afafaf, 0 16px 0 0 #afafaf; }
  .nav--closed .nav__toggle:hover::before {
    background-color: black;
    box-shadow: 0 8px 0 0 black, 0 16px 0 0 black; }
  .nav--closed .nav__toggle:active::before {
    background-color: black;
    box-shadow: 0 8px 0 0 black, 0 16px 0 0 black; }
  .nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 13px;
    width: 27px;
    height: 2px;
    background-color: #afafaf; }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .nav--opened .nav__toggle::after {
    transform: rotate(-45deg); }
  .nav--opened .nav__toggle:active::before, .nav--opened .nav__toggle:active::after {
    background-color: black; }
  .nav--opened .nav__toggle--close {
